

.product{
    display: flex;
    flex-direction: column;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    z-index: 1;
    


}

.product__price{
    margin: 5px;

}

.product__info{
    height: 100px;
    margin: 15px;

}

.product__rating{
    display: flex;
}

.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product >button{
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734, #9c7e31, #846a29 ;
}